@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
* {
  font-family: "Sora", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.home,
.activity,
.videos,
.birthday,
.milestone,
.taiwan,
.english,
.fanarts,
.map,
.about {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  background-image: url(/ssrbfansite/static/media/bg.c51f20e5.png);
  background-position: inherit;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  text-shadow: 2px 2px black;
  font-size: 100px;
  z-index: 9999;
}

.anniversary {
  display: flex;
  height: 100vh;
  align-items: top;
  justify-content: center;
  font-size: 3rem;
  background-image: url(/ssrbfansite/static/media/bg.c51f20e5.png);
  background-position: inherit;
  background-size: cover;
  background-repeat: no-repeat;
  text-shadow: 2px 2px black;
  font-size: 100px;
}

.anniversary_anchor {
  color: #fff;
  height: 30%;
  margin-top: 80px;
  padding-top: 20px;
}

.fa-external-link-square-alt {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

iframe {
  display: block;
  width: 50%;
  height: 50%;
  position: absolute;
  top: 25%;
  left: 25%
}

.projects {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  height: 100vh;
  align-items: top;
  justify-content: center;
  font-size: 3rem;
  background-image: url(/ssrbfansite/static/media/bg.c51f20e5.png);
  background-position: inherit;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  text-shadow: 2px 2px black;
  font-size: 100px;
}

.project_col {
  display: flex;
  flex: 1 0 18%;
  margin: 5px;
  border: 1px solid #fff;
  border-radius: 10px;
  color: white;
  background: tomato;
  background: rgba(255, 255, 255, 0.5);
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-out;
  line-height: 35px;
  font-size: 30px;

  background-position: center;
  /* height: 70%; */
  /* align-self: center; */
}

.project_col:after {
  position: absolute;
  top: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  white-space: pre-wrap;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0 8px;
  opacity: 0;
  content: attr(data-text);
  transition: all 0.3s ease-out;
  font-size: 20px;
}

.project_col:hover:after {
  opacity: 1;
  top: 0;
  padding: 8px;
  border-radius: 10px;
  font-size: 20px;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.project_title {
  display: flex;
  flex-direction: column;
  color: #fff;
  opacity: 1;
  transition: all 0.3s ease-out;
}


.project_col:hover {
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  transition: all 0.3s ease-in;
}

.project_col:hover .project_title {
  opacity: 0;
  transition: all 0.3s ease-in;
}


.botan_img {
  -webkit-animation: botan_img 2s cubic-bezier(.39, -1.05, .58, 1.95);
          animation: botan_img 2s cubic-bezier(.39, -1.05, .58, 1.95);
  -webkit-transform-origin: center bottom;
          transform-origin: center bottom;
  position: absolute;
  bottom: 0;
  z-index: 1;
  max-height: 91.5vh;
}

@-webkit-keyframes botan_img {
  0% {
    opacity: 0;
    -webkit-transform: scale3d(.75, .75, 1);
            transform: scale3d(.75, .75, 1);
  }

  40% {
    opacity: 0;
    -webkit-transform: scale3d(.75, .75, 1);
            transform: scale3d(.75, .75, 1);
  }

  100% {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

@keyframes botan_img {
  0% {
    opacity: 0;
    -webkit-transform: scale3d(.75, .75, 1);
            transform: scale3d(.75, .75, 1);
  }

  40% {
    opacity: 0;
    -webkit-transform: scale3d(.75, .75, 1);
            transform: scale3d(.75, .75, 1);
  }

  100% {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
}

.botan_info {
  display: flex;
  flex: 1 1;
  margin: 0 auto;
  border: 1px solid #fff;
  border-radius: 10px;
  color: white;
  background: tomato;
  background: rgba(255, 255, 255, 0.5);
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-out;
  font-size: 30px;
  max-width: 40%;
  -webkit-animation: botan_img 3s cubic-bezier(.39, -1.05, .58, 1.95);
          animation: botan_img 3s cubic-bezier(.39, -1.05, .58, 1.95);
  -webkit-transform-origin: center bottom;
          transform-origin: center bottom;
}

