.map p {
  align-items: center;
  justify-content: center;
  width: 60%;
}

.map svg {
  display: block;
  align-items: center;
  justify-content: center;
  -webkit-transform: scale(1.3, 1.2);
          transform: scale(1.3, 1.2);
  background-color: #C3F4FE;
  margin: auto;
  border-radius: 10px;
}

.map>element.style {
